@import '../includes/variables';
@import '../includes/mixins';

.block-nos-competences {
    padding: 80px 0;
    background-color: $black;
    color: $white;

    .link {
        color: $white;
    }

    .list-item {
        display: flex;
        margin-top: 40px;

        .item {
            width: calc(33.33% - 20px);

            &:not(:first-child) {
                margin-left: 30px;
            }

            .icon {
                margin-bottom: 20px;
            }

            .title-item {
                font-size: 24px;
                line-height: 44px;
                margin-bottom: 10px;
            }

            .descriptions {
                opacity: 0.6;
            }
        }
    }
}